	</div>
</template>

<script>
export default {
  name: 'location',
  data () {
    return {
    	pazzaImg:require('../../assets/pazza1.jpg')
    }
  },
  components:{
  }, 
  
  methods:{
    
  },
  mounted(){
    
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .location{
    margin-top: 8px;
    min-height: 500px;
  }
  #map{
    width:100%;
    /*height:400px;*/
    /*border:#ccc solid 1px;*/
    font-size:12px; 
    border:none; 
    float:left;
    border-radius: 5px;
    overflow: hidden;
  }
  #map img{
    width: 100%;
  }
</style>



